    app.reg = function(regInfo, callback) {
        callback = callback || $.noop;
        regInfo = regInfo || {};
        regInfo.mobile = regInfo.mobile || '';
        regInfo.password = regInfo.password || '';
        /************************************************************************/
        /*                                                           */
        /************************************************************************/
        regInfo.device = plus.device.vendor || ''; //
        regInfo.model = plus.device.model || ''; //
        regInfo.screen = plus.screen.resolutionWidth + ',' + plus.screen.resolutionHeight || ''; //
        regInfo.sys = plus.os.name || ''; //
        regInfo.version = plus.os.version || ''; //
        regInfo.no = plus.device.uuid || ''; //id
        /************************************************************************/
        /*                              */
        /************************************************************************/
        var noNull = /^$/;
        if (noNull.test(regInfo.mobile)) {
            return callback('');
        }
        /************************************************************************/
        /*                              */
        /************************************************************************/
        if (noNull.test(regInfo.password)) {
            return callback('');
        }
        /************************************************************************/
        /*                              */
        /************************************************************************/
        if (noNull.test(regInfo.code)) {
            return callback('');
        }


        mui.ajax('https://api.gaoqi.cespc.com:9378/user/register', {
            data: regInfo,
            dataType: 'json', //json
            type: 'post', //HTTP
            timeout: 10000, //10
            success: function(data) {
                if (data.ret == 1) {
                    console.log('');
                    // var obj = {};  
                    // obj.ticket = data.ticket;
                    // obj.expire_timestamp = data.expire_timestamp;
                    // obj.user = data.user;
                    // app.setState(obj);
                    console.log(JSON.stringify(data));
                    app.setReginfo('ticket', data.ticket);// 
                    app.setReginfo('expire_timestamp', data.expire_timestamp);
                    app.setReginfo('user', data.user);
                    console.log(JSON.stringify(app.getReginfo()));
                    console.log('ticket user expire_timestamp ');
                    return callback();
                } else {
                    /* */
                    if (data.ret == '20204' || data.ret == 20204) {
                        return callback(data.ret);
                    }
                    callback(data.err);
                }
            },
            error: function(xhr, type, errorThrown) {
                //
                if (type == 'timeout') {
                    plus.nativeUI.toast('');
                } else if (type == 'abort') {
                    plus.nativeUI.toast('');
                } else if (type == 'timeout') {
                    plus.nativeUI.toast('');
                }
                console.log(type);
            }
        });
        //20204    
    };

