    app.getverificationCode = function(phonenum, callback) {
        console.log('');
        console.log(phonenum);
        mui.ajax('https://api.gaoqi.cespc.com:9378/user/register/sendsms', {
            data: {
                'mobile': phonenum
            },
            dataType: 'json', //json
            type: 'post', //HTTP
            timeout: 10000, //10
            success: function(data) {
                if (data.ret == 1) {
                    console.log('');
                    return callback();
                } else {
                    return callback(data.err);
                }
            },
            error: function(xhr, type, errorThrown) {
                //
                if (type === 'timeout') {
                    plus.nativeUI.toast('');
                } else if (type === 'abort') {
                    plus.nativeUI.toast('');
                } else if (type === 'timeout') {
                    plus.nativeUI.toast('');
                }
                console.log(type);
            }
        })
    };
    app.gotoLogView = function(){
            app.setState(null);//
            mui.openWindow({
                url: plus.webview.getWebviewById( plus.runtime.appid ).getURL(),//
                id: plus.webview.getWebviewById( plus.runtime.appid ),//id
                preload: true,
                show: {
                    aniShow: 'pop-in'
                },
                styles: {
                    popGesture: 'hide'
                },
                waiting: {
                    autoShow: false
                },
                extras: { //main
                    main_loaded: 'true'
                }
            });
    }
export {app}





