<template>
    <div>
      <header class="mui-bar mui-bar-nav">
        <h1 class="mui-title"></h1>
      </header>
      <div class="mui-content">
          <form id='login-form' class="mui-input-group">
              <div class="mui-input-row">
                  <label class="iconuser">&nbsp;</label>
                  <input id='account' type="text" class="mui-input-clear mui-input" placeholder="">
              </div>
              <div class="mui-input-row">
                  <label class="iconpassword">&nbsp;</label>
                  <input id='password' type="password" class="mui-input-clear mui-input" placeholder="">
              </div>
          </form>
          <div>
              <button id='login' class="mui-btn mui-btn-block mui-btn-cheng"> </button>
              <div class="link-area"><a id='forgetPassword'></a></div>
          </div>
          <div class="bottom_area">
              <button id='reg' class="mui-btn-block mui-btn-zc"></button>
              <div class="oauth-area">
              </div>
          </div>
      </div>
    </div>
    
</template>

<script>
      require("expose-loader?mui!assets/js/mui.js");
      require("expose-loader?enterfocus!assets/js/mui.enterfocus.js");
      import {app} from 'assets/js/appp.js'
        mui.init({
            statusBarBackground: '#f7f7f7',
        });
        mui.plusReady(function() {

            //
            plus.screen.lockOrientation("portrait-primary");
            //setting
            var settings = app.getSettings();
            // token
            var state = app.getState();
            /************************************************************************/
            /*                 */
            /************************************************************************/
            //
            var showGuide = plus.storage.getItem("lauchFlag");
            if (!showGuide) {
                mui.openWindow({
                    id: 'html/guide.html',
                    url: 'html/guide.html',
                    styles: {
                        popGesture: "none"
                    },
                    show: {
                        aniShow: 'none'
                    },
                    waiting: {
                        autoShow: false
                    }
                });
            };
            /************************************************************************/
            /* tomain                            */
            /************************************************************************/
            var mainPage = mui.preload({
                "id": 'index.html',
                "url": 'index.html'
            });
            var main_loaded_flag = false;
            // 
            var self = plus.webview.currentWebview();
            if(self.main_loaded){
                main_loaded_flag = true;
            }
            mainPage.addEventListener("loaded", function() {
                main_loaded_flag = true;
            });
            var toMain = function() {
                //
                //mainloaded
                var id = setInterval(function() {
                    if (main_loaded_flag) {
                        console.log('');
                        clearInterval(id);
                        mui.fire(mainPage, 'show', null);
                        mainPage.show("pop-in");
                    } else {
                        console.log("");
                    }
                }, 20);
            };

            /************************************************************************/
            /*                                           */
            /************************************************************************/
            var time = parseInt(app.getState().expire_timestamp); //token
            var date = parseInt(Date.parse(new Date())) / 1000;
            var isExpired = (time > date) ? true : false; // true false
            if (time && isExpired) {
                //   token 
                //    
                //
                console.log('');
                isPerfectAndtoMain();
                // console.log(JSON.stringify(app.getState()));
            } else {
                app.setState(null);
                
            }
            
            /************************************************************************/
            /*                                       */
            /************************************************************************/
            var loginButton = document.getElementById('login');
            var accountBox = document.getElementById('account');
            var passwordBox = document.getElementById('password');
            var regButton = document.getElementById('reg');
            var forgetButton = document.getElementById('forgetPassword');
            loginButton.addEventListener('tap', function(event) {
                var loginInfo = {
                    mobile: accountBox.value,
                    password: passwordBox.value
                };
                app.login(loginInfo, function(err) {
                    if (err) {
                        plus.nativeUI.toast(err);
                        return;
                    }
                    isPerfectAndtoMain();
                });
            });
            mui.enterfocus('#login-form input', function() {
                mui.trigger(loginButton, 'tap');
            });

            var regObj = mui.preload({
                "id": 'reg.html',
                "url": 'reg.html'
            });
            var missObj = mui.preload({
                "id": 'forget_password.html',
                "url": 'forget_password.html'
            });

            regButton.addEventListener('tap', function(event) {
                plus.webview.show(regObj, 'pop-in', 250);
            }, false);
            forgetButton.addEventListener('tap', function(event) {

                plus.webview.show(missObj, 'pop-in', 250);
            }, false);
            //
            window.addEventListener('resize', function() {
                oauthArea.style.display = document.body.clientHeight > 400 ? 'block' : 'none';
            }, false);
            //
            var backButtonPress = 0;
            mui.back = function(event) {
                backButtonPress++;
                if (backButtonPress > 1) {
                    plus.runtime.quit();
                } else {
                    plus.nativeUI.toast('');
                }
                setTimeout(function() {
                    backButtonPress = 0;
                }, 1000);
                return false;
            };

            function isPerfectAndtoMain() {
                var userdata = app.getState();
                if (!userdata.user.is_perfect || userdata.user.is_perfect != 1) {
                    console.log("");
                    plus.nativeUI.toast('');
                    // app.setReginfo('ticket', userdata.ticket);
                    //
                    mui.openWindow({
                        url: 'information.html',
                        id: 'information.html',
                        preload: true,
                        show: {
                            aniShow: 'pop-in'
                        },
                        styles: {
                            popGesture: 'hide'
                        },
                        waiting: {
                            autoShow: false
                        }
                    });
                } else {
                    console.log(' 11');
                    toMain();
                }
            }
        })
export default {
            
          }
</script>
<style>
  
</style>



